@use "./Shared.scss" as shared;
*, *:before, *:after{
  box-sizing: border-box;
}

body, html {
  margin:0;
  padding:0;
  background-color: white;
  height: 100%;
}
h1, h2, h3, h4, h5, h6 {
  color: shared.$secondary-color;
  margin: 0.4em 0;
}
p{
  margin: 0.3em 0;
}
a {
  color: shared.$accent-color;
  // text-decoration: none;
}

*{
  font-family: shared.$primary-font-fam;
}

