@use "./../../styles/Shared" as shared;


$default_margin: 0.6em;
$PP_size: 16vw;

.MyCV{

  .introductary_informations{
    border-block: 0.2em solid shared.$secondary-color;
    display: grid;
    grid-template-columns: 25% 75%;
    align-items: center;
    background-color: shared.$primary-color;
    
    .personality_picture {
      place-self: center;
      display: flex;
      justify-content: center;

      margin: 1em;
      width: $PP_size;
      height: $PP_size;
      overflow: hidden;

      
      border-radius: 50%;
      border: $default_margin solid;
      outline: 0.2em solid shared.$primary-color;
      border-color: shared.$secondary-color ;//shared.$accent-color shared.$accent-color shared.$secondary-color;
      

      img{
        max-width: 100%;
        margin: auto;
      }
    }

    .imatricule{
      display: flex;
      align-items: center;
      margin: $default_margin;

      .qr_codes{
        display: flex;
        justify-content: end;
        width: 100%;
        height: 100%;
        
        div.qr_code{
          margin: $default_margin;
          border: 1em solid white;
          outline: 1px solid shared.$accent-color;
          position: relative;
          
          &:hover{
           box-shadow: 0 0 0.3em shared.$accent-color;
          }

          & canvas{
            height: 100% !important;
            width: 100% !important;
          }
          & img{
            background-color: white;
            border: 0.2em solid white;
            border-radius: 50%;
            // margin: $default_margin;
            
            $img_size: 30%;
            width: $img_size; //picture size + border size
            position: absolute;
            left: calc(50% - $img_size/2);
            top: calc(50% - $img_size/2);
          }
        }
      }
    }

    p{
      color: shared.$secondary-color;
    }
  }

  .experience_content{
    display: grid;
    justify-content: space-around;
    grid-template-columns: 25% 75%;

    .personality_profile {
      // display: flex;
      // flex-direction: column;
      align-items: center;
      background-color: shared.$primary-color;
      border-right: 0.2em solid shared.$secondary-color;
      color: black;
        

      .profile_interests{
        margin: 0 $default_margin;

        .profile_interest{
          margin: $default_margin 0;

          .interest_header{
            display: flex;
            justify-content: space-between;
          }
        }
        
      }

      h2{
        font-size: shared.$emH4;
      }
      h4{
        font-size: shared.$emH6;
      }
      p{
        font-size: shared.$emH7;
        font-weight: lighter;
      }
  
    }
  
    .work_profile{
      color: black;
  
      .work_experiences{
        margin: 0 $default_margin;
  
        .work_experience{
          margin: $default_margin 0;
          // border: solid;

          header{
            display: grid;
            grid-template-columns: 60% 40%;
            h3:last-child{
              text-align: end;
            }
          }
          ul{
            list-style: none;
            padding-left: 0;
            margin: 0;
          }

          .work_project{
            margin-block: $default_margin;
            li{
              margin-block: calc($default_margin/2);
            }
          }
        }
  
      }
    }
  }
}


.separator{
  // border-top: 3px inset;
  width: 50%;
  border: 0.1em solid;
  border-radius: 0.1em;
  border-color: shared.$accent-color;
}

.progress_out{
  background-color: shared.$primary-color;
  height: $default_margin;
  border-radius: $default_margin;
  border: 1px solid shared.$accent-color;

  &>.progress_in{
    background-color: shared.$accent-color;
    height: 100%;
    border-radius: inherit;
    font-size: $default_margin;
    color: shared.$primary-color;
  }
}
