@use "./../styles/Shared";

nav {
  // border-bottom: 0.2em solid Shared.$accent-color;
  // border-block: 0.2em solid Shared.$accent-color;
  background-color: Shared.$secondary-color;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  ul {
    list-style: none;
    display: flex;
    margin: 0;
    padding-left: 1em;

    li {
      margin: 1em;

      a {
        text-decoration: none;
        color: Shared.$primary-color;

        &.active {
          border-bottom: 4px solid;
        }
      }
    }
  }
}
  
